import React from 'react';
import './Notice.scss';
import './Movie.scss';

const Movie = (props) => {

    const {close} = props;

    return(
        <div className='modal'>
            <div className='modal2'>
                <h2><img src="/icon.png" alt='icon_img'/><span></span></h2>
                    <div className='container'>
                        <div className='item'></div>
                        <div className='item'></div>
                        <div className='item'></div>
                        <div className='item'></div>
                        <div className='item'></div>
                        <div className='item'></div>
                    </div>
                <p>
                    <button style={{float:'right', padding:'0 3%'}}></button>
                    <button style={{float:'right', padding:'0 3%', marginRight:'3%'}}></button>
                </p>
                <p>
                    <button style={{float:'right', padding: '0 3%'}} onClick={()=>{close(false)}}></button>
                    <button style={{float:'left', padding:'0 3%', marginRight:'3%'}}></button>
                </p>
                
            </div>
        </div>
    )
}

export default Movie;