import React, {useState} from 'react';
import './Join.scss';
import './Notice.scss';
import axios from 'axios';
import QueryString from 'querystring';

// 
const Join = (props) => {

    const {close} = props; // 

    const [email, setEmail] = useState('');
    const [id, setId] = useState('');
    const [pw, setPw] = useState('');
    const [pw_check, setPw_check] = useState('');
    const [phone, setPhone] = useState('');

    const [formCheck, setFormCheck] = useState({
        email:false,
        id: false, 
        pw: false,
        pwCheck: false,
    });

    const joinAcess = () => { //  
        if(formCheck.email && formCheck.id && formCheck.pw && formCheck.pwCheck){
            axios({
                method:'post', //method:   
                url:'https://kimgtae12.cafe24.com/ajax/ajax_join_update.php',
                data:QueryString.stringify({email:email, id:id, pw:pw, phone:phone, pw_check:pw_check}),
            })
            .then(res=>res.data) //res:  , res.data: 
            .then(data=>{
                if(data.result){
                    alert(' .');
                    sessionStorage.setItem('id', id);
                }
                else{
                    alert(data.msg);
                }
            }) // 
        }else if(!formCheck.email){
            alert('    .');
            console.log(formCheck)
        }else if(!formCheck.id){
            alert('     .');
        }else if(!formCheck.pw){
            alert(' 8~20   .');
        }else if(!formCheck.pwCheck){
            alert('  .   .');
        }
        
    }

    const emailCheck = () => { //  
        axios({
            method:'post', //method:   
            url:'https://kimgtae12.cafe24.com/ajax/email_check.php',
            data:QueryString.stringify({email:email}),
        })
        .then(res=>res.data) //res:  , res.data: 
        .then(data=>{
            if(data.result){
                setFormCheck({
                    ...formCheck,
                    email:true
                })
                alert(data.msg);
            }
            else{
                setFormCheck({
                    ...formCheck,
                    email:false
                })
                alert(data.msg);
            }
        })
    }

    const idCheck = () => { //  
        axios({
            method:'post', //method:   
            url:'https://kimgtae12.cafe24.com/ajax/join_id_check.php',
            data:QueryString.stringify({id:id}),
        })
        .then(res=>res.data) //res:  , res.data: 
        .then(data=>{
            if(data.result){
                setFormCheck({
                    ...formCheck,
                    id:true
                })
                alert(data.msg);
            }
            else{
                alert(data.msg);
            }
        })
    }

    return(
        <div className='modal'>
            <div className='modal2'>
            <h2><img src="/icon.png" alt='icon_img'/><span></span></h2>
            <form className='joinForm'>
                <p><label htmlFor='email'>: </label> {/* for   input id . */}
                <input type='text' id='email' placeholder=' ' value={email} onChange={(event)=>{
                    setEmail(event.target.value)
                }}/>
                <button type='button' style={{marginRight:'5vw', padding:'0.5% 3%'}} className='joinBtn' onClick={()=>emailCheck()}></button>
                </p>
                <p><label htmlFor='id'>: </label>
                <input type='text' id='id' placeholder=' ' value={id} onChange={(event)=>{
                    setId(event.target.value)
                }}/>
                <button type='button' style={{marginRight:'5vw', padding:'0.5% 3%'}} className='joinBtn' onClick={()=>idCheck()}></button>
                </p>
                <p><label htmlFor='pw'>: </label> 
                <input type='password' id='pw' placeholder=' ' value={pw} onChange={(event)=>{
                    setPw(event.target.value)
                    if( 8 <= event.target.value.length && event.target.value.length <= 20){
                        setFormCheck({...formCheck, pw:true})
                    }else{
                        setFormCheck({...formCheck, pw:false})
                    }
                }}/></p>
                <p><label htmlFor='pw_check'> : </label> 
                <input type='password' id='pw_check' placeholder='  ' value={pw_check} onChange={(event)=>{
                    setPw_check(event.target.value);
                    console.log(formCheck);
                    setFormCheck({...formCheck, pwCheck : event.target.value == pw});
                }}/></p>
                <p><label htmlFor='phone'>: </label> 
                <input type='text' id='phone' placeholder='  (- )' value={phone} onChange={(event)=>{
                    setPhone(event.target.value)
                }}/></p>
            </form>
            <button style={{margin:'3vh 40% 0vh 1vw', padding:'0.5% 3%'}} onClick={()=>{close(false)}} className='joinBtn'></button>
            <button style={{margin:'3vh 0vw 0vh 0vw', padding:'0.5% 3%'}} className='joinBtn' onClick={() => joinAcess()}></button>
            </div>
        </div>
    )
}

// 
const Login = (props) => {
    
    const {close} = props;

    const [id, setId] = useState('');
    const [pw, setPw] = useState('');

    const login = () => {
        axios({
            method:'post', //method:   
            url:'https://kimgtae12.cafe24.com/ajax/ajax_login_check.php',
            data:QueryString.stringify({id:id, pw:pw}),
        })
        .then(res=>res.data) //res:  , res.data: 
        .then(data=>{
            console.log(data);
        if(data.result){
            alert(data.msg);
            sessionStorage.setItem('id', id);
        }
        else{
            alert(data.msg);
        }
        })
    }

    return(
        <div className='modal'>
            <div className='modal2'>
            <h2><img src="/icon.png" alt='icon_img'/><span></span></h2>
            <form className='joinForm'>
                <p><label htmlFor='id'>: </label>
                <input type='text' id='id' value={id} onChange={(e)=>{setId(e.target.value)}}/>
                </p>
                <p><label htmlFor='pw'>: </label> 
                <input type='password' id='pw' value={pw} onChange={(e)=>setPw(e.target.value)}/></p>
            </form>
            {/*  ,      (+ null ,     ) */}
            <button stype='button' style={{margin:'3vh 40% 0vh 1vw', padding:'0.5% 3%'}} onClick={()=>{close(false)}} className='joinBtn'></button>
            <button style={{margin:'3vh 0vw 0vh 0vw', padding:'0.5% 3%'}} className='joinBtn' onClick={()=>login()}></button>
            </div>
        </div>
    )
}

export {Join, Login};