import React, {useState} from 'react';
import './News.scss';
import Notice from '../hooks/Notice';
import Event from '../hooks/Event';
import Price from '../hooks/Price';


const News = () => {

    const [component, setComponent] = useState(false);
    const [type, setType] = useState(false); //small, medium, special, surcharge
    const [typeName, setTypeName] = useState(false); // , , ,  

    const handler = (name) => {
        setComponent(name);
        if(name === 'small'){
            setType(name);
            setTypeName('');
        }else if(name === 'medium'){
            setType(name);
            setTypeName('');
        }else if(name === 'special'){
            setType(name);
            setTypeName('');
        }else{
            setType(name);
            setTypeName(' ');
        }
    }
    

    return(
        <div className='News' id='4'> 
            <div className='NewsDiv'>
                <div className='item'>
                    <ul className='IconUl'><p className='icon'><img src="/icon.png" alt='icon_img'/>
                        <button onClick={()=>handler('notice')}>... </button></p>
                        {component === 'notice'? <Notice close={setComponent}/> : null}
                        <li><a>1</a></li>
                        <li><a>2</a></li>
                        <li><a>3</a></li>
                        <li><a>4</a></li>
                    </ul>
                </div>
                <div className='item'>
                    <ul className='IconUl'><p className='icon'><img src="/icon.png" alt='icon_img'/>
                        <button  onClick={()=>handler('event')}>... </button></p>
                        {component === 'event'? <Event close={setComponent}/> : null}
                        <li><a>1</a></li>
                        <li><a>2</a></li>
                        <li><a>3</a></li>
                        <li><a>4</a></li>
                    </ul>
                </div>
                <div className='item'>
                    <ul className='IconUl'><p className='icon'><img src="/icon.png" alt='icon_img'/></p>
                        <li><a onClick={()=>handler('small')} > </a></li>
                        <li><a onClick={()=>handler('medium')}> </a></li>
                        <li><a onClick={()=>handler('special')}> </a></li>
                        <li><a onClick={()=>handler('surcharge')}></a></li>
                        {component === 'small'? <Price close={setComponent} value={type} name={typeName}/>: null}
                        {component === 'medium'? <Price close={setComponent} value={type} name={typeName}/>: null}
                        {component === 'special'? <Price close={setComponent} value={type} name={typeName}/>: null}
                        {component === 'surcharge'? <Price close={setComponent} value={type} name={typeName}/>: null}
                    </ul>
                </div>
            </div>
        </div>
    );
}

export default News;