import React, {useState} from 'react';
import './Notice.scss';

const NoticeWrite = (props) => {
    
    const {close} = props; //NoticeWrite 
    const [noticeContent, setNoticeContent] = useState({ //NoticeWrite  
        noticeTitle: '',
        noticeText: ''
    });

    const getValue = e => {
        const {name, value} = e.target; //e.name(noticeTitle, noticeText), e.value(input value)
        setNoticeContent({
            ...noticeContent, //noticeContent 
            [name]: value
        })
        console.log(noticeContent);
    };

    return(
        <div className='modal'>
            <div className='modal2' >
                <h2><img src="/icon.png" alt='icon_img'/><span></span></h2>
                <h4 style={{display:'inline-block', marginRight:'1vw', color:'#d3a875'}}> </h4>
                    <input type='text' style={{width:'80%'}} onChange={getValue} name='noticeTitle'/>
                <h4 style={{marginTop:'0%', color:'#d3a875'}}></h4>
                    <textarea style={{marginTop:'-2%', width:'88.5%', height:'50%'}} onChange={getValue} name='noticeText'></textarea>
                <button style={{margin:'1vh 4vw 0vh 1vw', padding:'0.5% 3%'}} onClick={()=>{close(false)}}></button>
                <button style={{margin:'1vh 0vw 0vh 3vw', padding:'0.5% 3%'}}></button>
            </div>
        </div>
    )
}

export default NoticeWrite;