import React, {useState} from 'react';
import './Notice.scss'; //modal
import './Price.scss';

const Price = (props) => {

    const {close} = props;

    const [type, setType] = useState(props.value); //State
    const [typeName, setTypeName] = useState(props.name); 

    const typeOptions = [
        {value: 'small', label:''},
        {value: 'medium', label:''},
        {value: 'special', label:''},
        {value: 'surcharge', label:' '},
    ]

    const onTypeChange = (e) =>{ //option   .
        const {value} = e.target;
        setType(value);
        if(value === 'small'){
            setTypeName(typeOptions[0].label)
        }else if(value === 'medium'){
            setTypeName(typeOptions[1].label)
        }else if(value === 'special'){
            setTypeName(typeOptions[2].label)
        }else{
            setTypeName(typeOptions[3].label)
        }
    }

    const resultType = () =>{
        if(type === 'small'){
            return(
                <div className='gridTable'>
                    <div className='item'><p className='title'></p></div>
                    <div className='item'>
                        <p className='content'></p>
                        <p className='content'></p>
                        <p className='content'></p>
                        <p className='content'></p></div>
                        <div className='item'><p className='title'>~4KG</p></div>
                    <div className='item'>
                        <p className='content'>30,000</p>
                        <p className='content'>20,000</p>
                        <p className='content'>50,000</p>
                        <p className='content'>60,000</p>
                    </div>
                    <div className='item'><p className='title'>~6KG</p></div>
                    <div className='item'>
                        <p className='content'>35,000</p>
                        <p className='content'>25,000</p>
                        <p className='content'>55,000</p>
                        <p className='content'>65,500</p>
                    </div>
                    <div className='item'><p className='title'>~8KG</p></div>
                    <div className='item'>
                        <p className='content'>40,000</p>
                        <p className='content'>30,000</p>
                        <p className='content'>60,000</p>
                        <p className='content'>70,000</p>
                    </div>
                    <div className='item'><p className='title'>~10KG</p></div>
                    <div className='item'>
                        <p className='content'>45,000</p>
                        <p className='content'>35,000</p>
                        <p className='content'>65,000</p>
                        <p className='content'>75,000</p>
                    </div>
                </div>
            )
        }else if(type === 'medium'){
            return(
                <div className='gridTable'>
                    <div className='item'><p className='title'></p></div>
                    <div className='item'>
                        <p className='content'></p>
                        <p className='content'></p>
                        <p className='content'></p>
                        <p className='content'></p></div>
                        <div className='item'><p className='title'>~4KG</p></div>
                    <div className='item'>
                        <p className='content'>35,000</p>
                        <p className='content'>25,000</p>
                        <p className='content'>55,000</p>
                        <p className='content'>65,000</p>
                    </div>
                    <div className='item'><p className='title'>~6KG</p></div>
                    <div className='item'>
                        <p className='content'>40,000</p>
                        <p className='content'>30,000</p>
                        <p className='content'>60,000</p>
                        <p className='content'>70,500</p>
                    </div>
                    <div className='item'><p className='title'>~8KG</p></div>
                    <div className='item'>
                        <p className='content'>45,000</p>
                        <p className='content'>35,000</p>
                        <p className='content'>65,000</p>
                        <p className='content'>75,000</p>
                    </div>
                    <div className='item'><p className='title'>~10KG</p></div>
                    <div className='item'>
                        <p className='content'>50,000</p>
                        <p className='content'>40,000</p>
                        <p className='content'>70,000</p>
                        <p className='content'>80,000</p>
                    </div>
                </div>
            )
        }else if(type === 'special'){
            return(
                <div className='gridTable'>
                    <div className='item'><p className='title'></p></div>
                    <div className='item'>
                        <p className='content'></p>
                        <p className='content'></p>
                        <p className='content'></p>
                        <p className='content'></p></div>
                        <div className='item'><p className='title'>~4KG</p></div>
                    <div className='item'>
                        <p className='content'>35,000</p>
                        <p className='content'>25,000</p>
                        <p className='content'>70,000</p>
                        <p className='content'>80,000</p>
                    </div>
                    <div className='item'><p className='title'>~6KG</p></div>
                    <div className='item'>
                        <p className='content'>40,000</p>
                        <p className='content'>30,000</p>
                        <p className='content'>75,000</p>
                        <p className='content'>90,500</p>
                    </div>
                    <div className='item'><p className='title'>~8KG</p></div>
                    <div className='item'>
                        <p className='content'>45,000</p>
                        <p className='content'>35,000</p>
                        <p className='content'>80,000</p>
                        <p className='content'>100,000</p>
                    </div>
                    <div className='item'><p className='title'>~10KG</p></div>
                    <div className='item'>
                        <p className='content'>50,000</p>
                        <p className='content'>40,000</p>
                        <p className='content'>85,000</p>
                        <p className='content'>110,000</p>
                    </div>
                </div>
            )
        } else{
            return(
                <div className='surcharge'>
                    <div className='item'><p className='title'></p></div>
                    <div className='item'><p className='content'>+5,000</p></div>
                    <div className='item'><p className='title'></p></div>
                    <div className='item'><p className='content'>+10,000</p></div>
                    <div className='item'><p className='title'>, </p></div>
                    <div className='item'><p className='content'>+15,000</p></div>
                    <div className='item'><p className='title'>, , </p></div>
                    <div className='item'><p className='content'>+10,000</p></div>
                    <div className='item'><p className='title'>, , </p></div>
                    <div className='item'><p className='content'>+10,000</p></div>
                    <div className='item'><p className='title'></p></div>
                    <div className='item'><p className='content'>+10,000</p></div>
                    <div className='item'><p className='title'></p></div>
                    <div className='item'><p className='content'>+10,000</p></div>
                    <div className='item'><p className='title'> (6mm,1cm,2cm)</p></div>
                    <div className='item'><p className='content'>+5,000</p></div>
                    <div className='item'><p className='title'>(, )</p></div>
                    <div className='item'><p className='content'>+5,000 ~</p></div>
                </div>
            )
        }    
    }

   

    return(
        <div className='modal'>
            <div className='modal2'>
                <h2><img src="/icon.png" alt='icon_img'/>
                    <span>
                        { typeName === ' ' ? typeName: typeName + ' '}
                        {/* {typeName}  */}
                    </span>
                </h2>
                    <select className='petPrice'
                        value = {type}
                        onChange={onTypeChange}>
                        {
                            typeOptions.map(type => (
                                <option value={type.value} key={type.label}>{type.label}</option>
                            ))
                        }
                    </select>
                    <div className='resultPrice'>
                        {resultType()}
                    </div>
                    <button style={{float:'right', padding:'0.5% 3%', margin:'2% 3vw'}} onClick={()=>{close(false)}}></button>
            </div>
        </div>
    )
}

export default Price;